
@charset "UTF-8";
/*圆点loading*/
.fading {
  position: fixed;
  left: 50%;
  top: 50%;
  margin-top: -50px;
  margin-left: -50px;
  width: 100px;
  height: 100px;
}
.fading-circle {
  width: 100%;
  height: 100%;
  position: absolute;
}
.fading-circle:before {
    content: '';
    display: block;
    margin: 0 auto;
    width: 15%;
    height: 15%;
    border-radius: 50%;
    background: #9e9e9e;
}
.fading-circle.one {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    -webkit-animation: fading 1.4s ease-in-out -0.2s infinite;
            animation: fading 1.4s ease-in-out -0.2s infinite;
}
.fading-circle.two {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    -webkit-animation: fading 1.4s ease-in-out -0.4s infinite;
            animation: fading 1.4s ease-in-out -0.4s infinite;
}
.fading-circle.third {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    -webkit-animation: fading 1.4s ease-in-out -0.6s infinite;
            animation: fading 1.4s ease-in-out -0.6s infinite;
}
.fading-circle.four {
    -webkit-transform: rotate(135deg);
            transform: rotate(135deg);
    -webkit-animation: fading 1.4s ease-in-out -0.8s infinite;
            animation: fading 1.4s ease-in-out -0.8s infinite;
}
.fading-circle.five {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    -webkit-animation: fading 1.4s ease-in-out -1s infinite;
            animation: fading 1.4s ease-in-out -1s infinite;
}
.fading-circle.six {
    -webkit-transform: rotate(225deg);
            transform: rotate(225deg);
    -webkit-animation: fading 1.4s ease-in-out -1.2s infinite;
            animation: fading 1.4s ease-in-out -1.2s infinite;
}
.fading-circle.seven {
    -webkit-transform: rotate(270deg);
            transform: rotate(270deg);
    -webkit-animation: fading 1.4s ease-in-out -1.4s infinite;
            animation: fading 1.4s ease-in-out -1.4s infinite;
}
.fading-circle.eight {
    -webkit-transform: rotate(315deg);
            transform: rotate(315deg);
    -webkit-animation: fading 1.4s ease-in-out -1.6s infinite;
            animation: fading 1.4s ease-in-out -1.6s infinite;
}
@-webkit-keyframes fading {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@keyframes fading {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}



