
.quiz-expert-info {
  height: 40px;
  line-height: 20px;
  margin-left: 52px;
  font-size: 0.9375rem;
}
.quiz-expert-info p {
    height: 20px;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
}
.quiz-expert-info .data {
    font-size: 0.875rem;
    color: rgba(0, 0, 0, 0.38);
}



