
.article-detail {
  padding-bottom: 53px;
}
.article-detail-cover {
    height: 188px;
}
.article-detail-header {
    position: relative;
}
.article-detail-tag {
    position: absolute;
    bottom: 10px;
    left: 16px;
}
.article-detail-tag li {
      float: left;
      height: 26px;
      padding: 0px 8px;
      color: #fff;
      font-size: 0.875rem;
      line-height: 26px;
      background: #000;
      opacity: 0.7;
      border-radius: 80px;
}
.article-detail-title {
    padding: 16px 16px 6px;
    line-height: 32px;
    font-size: 1.5rem;
    word-wrap: break-word;
    font-weight: 500;
    border-bottom: 1px solid #f2f2f2;
}
.article-detail-author {
    padding: 10px 16px;
    border-bottom: 1px solid #f2f2f2;
}
.article-detail-author-fixed {
    box-sizing: border-box;
    width: 100%;
    position: fixed;
    top: 0;
    padding: 10px 16px;
    border-bottom: 1px solid #f2f2f2;
}
.article-detail .question-btn {
    width: 70px;
    height: 30px;
    margin-top: 15px;
    margin-right: 16px;
    line-height: 30px;
    font-size: 0.875rem;
    color: #0af;
    text-align: center;
    border-radius: 2px;
    border: 1px solid #0af;
}
.article-detail-content {
    padding: 6px 16px;
    font-size: 1rem;
    line-height: 27px;
    color: #212121;
}
.article-detail-content div, .article-detail-content p, .article-detail-content li {
      margin-bottom: 22px;
}
.article-detail-content img {
      width: 100%;
      margin-bottom: 16px;
}
.article-detail-date {
    padding: 2px 16px 22px;
    font-size: 0.8125rem;
    line-height: 14px;
    text-align: right;
    color: #9e9e9e;
}
.article-detail-date p {
      margin-bottom: 8px;
}



