
.classify-detail-header {
  background: url(../../assets/images/special.jpg) no-repeat;
  background-size: 100% 100%;
}
.classify-detail-header h2 {
    padding: 52px 0 21px;
    line-height: 28px;
    font-size: 24px;
    font-weight: 500;
    text-align: center;
    color: #fff;
}
.classify-detail-focus {
  margin: 0 auto 30px;
}
.classify-detail-tab {
  background: transparent !important;
}
.classify-detail-tab ul li {
    color: rgba(255, 255, 255, 0.7) !important;
}
.classify-detail-tab ul li.active {
      color: #ffa42f !important;
}
.classify-detail-content {
  overflow: hidden;
  background: #f2f2f2;
}
.classify-detail-live {
  margin-top: 10px;
}
.classify-detail-expert {
  padding: 16px;
  margin-top: 10px;
  background: #fff;
}
.classify-detail-user-statics {
  margin: 10px 0 0 50px;
}



