
.quiz-write {
  background: #f2f2f2;
}
.quiz-write-content {
    padding: 16px;
    margin-bottom: 10px;
    background-color: #fff;
}
.quiz-write-content textarea {
      width: 100%;
      height: 24px;
      font-size: 1.125rem;
      line-height: 24px;
      border: none;
      outline: none;
      resize: none;
}
.quiz-write-content textarea.active {
        font-weight: 600;
}
.quiz-write-search {
    background: #fff;
}
.quiz-write-search-item {
      padding: 19px 16px 22px;
      border-bottom: 1px solid #f2f2f2;
}
.quiz-write-search-item em {
        color: #0af;
        font-style: normal;
}
.quiz-write-search-item .result {
        line-height: 21px;
        max-height: 42px;
        margin-bottom: 7px;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;
}



