
.index {
  background: #f2f2f2;
  margin-bottom: 53px;
}
.index-search {
    padding: 8px 16px !important;
    background: #fff;
    border-bottom: 1px solid #f2f2f2;
}
.index-search a {
      background: #f2f2f2;
}
.index-subimg {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 3.0625rem;
    border-top: 1px solid #f2f2f2;
}
.index-subimg li {
      color: rgba(0, 0, 0, 0.25);
}
.index-subimg i {
      width: auto;
      height: auto;
      font-size: 1.375rem;
      color: rgba(0, 0, 0, 0.25);
}



