
.nav-search {
  padding: 0 16px;
  color: rgba(255, 255, 255, 0.5);
}
.nav-search-left {
    display: block;
    height: 36px;
    line-height: 36px;
    font-size: 0.9375rem;
    background: #fff;
    border-radius: 100px;
    cursor: pointer;
}
.nav-search-left i {
      margin: 0 3px 0 10px;
      font-size: 0.875rem;
}
.nav-search .right {
    position: absolute;
    top: 9px;
    right: 16px;
    width: 54px;
    height: 34px;
    font-size: 0.875rem;
    line-height: 34px;
    text-align: center;
    color: #fff;
    border-radius: 3px;
    background: #0af;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.15);
    outline: none;
    border: none;
    cursor: pointer;
}



