
.question-module {
  padding: 14px 16px;
  margin-top: 10px;
  background: #fff;
}
.question-module-title {
    margin: 4px 0 10px;
    line-height: 24px;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
}
.question-module-operate {
    margin-top: 20px;
}



