
.fine-module {
  margin-bottom: 10px;
  background: #fff;
}
.fine-module:last-child {
    margin-bottom: 0;
}
.fine-module-header {
    height: 40px;
    padding: 0 16px;
    line-height: 40px;
    font-size: 0.8125rem;
    border-bottom: 1px solid #f2f2f2;
}
.fine-module-title {
    margin: 6px 0 8px;
    font-size: 1.125rem;
    line-height: 23px;
    font-weight: 500;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
}



