
.detail-footer {
  position: fixed;
  bottom: 0;
  box-sizing: border-box;
  width: 100%;
  height: 52px;
  padding-left: 42px;
  background: #fff;
  border-top: 1px solid #f2f2f2;
}
.detail-footer-back {
    position: absolute;
    top: 0;
    left: 0;
    display: inline-block;
    width: 42px;
    height: 52px;
    line-height: 52px;
    color: #333333;
    text-align: center;
}
.detail-footer-item {
    float: left;
    width: 25%;
    text-align: center;
    font-size: 0;
}
.detail-footer-item i {
      display: inline-block;
      margin-top: 11px;
      height: 17px;
      overflow: hidden;
      font-size: 1.0625rem;
      line-height: 1.0625rem;
      color: rgba(0, 0, 0, 0.7);
}
.detail-footer-item .icon-dashang:before {
      position: relative;
      left: 1px;
}
.detail-footer-item .icon-shoucang1:before {
      position: relative;
      left: -1px;
}
.detail-footer-item .icon-pinglun:before {
      position: relative;
      top: 2px;
}
.detail-footer-item p {
      margin-top: 5px;
      font-size: 0.6875rem;
      line-height: 0.6875rem;
      color: #333333;
}



