
.special-detail {
  background: #f2f2f2;
}
.special-detail-header {
    width: 100%;
    padding: 40px 16px;
    text-align: center;
    background: url(../../assets/images/special.jpg) no-repeat;
    background-size: 100% 100%;
}
.special-detail-header .title {
      font-size: 1.375rem;
      font-weight: 500;
      line-height: 1.5rem;
      color: #fd9324;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      overflow: hidden;
      text-overflow: ellipsis;
}
.special-detail-header .instr {
      margin-top: 10px;
      font-size: 0.8125rem;
      line-height: 1.125rem;
      color: #fff;
      opacity: 0.6;
}
.special-detail-description {
    padding: 16px;
    font-size: 0.875rem;
    line-height: 1.5rem;
    background: #fff;
}
.special-detail-wellchosen {
    padding: 16px;
    margin-top: 10px;
    background: #fff;
}
.special-detail-operate {
    margin-top: 13px;
}



