
.user {
  padding-top: 10px;
  background-color: #f2f2f2;
}
.user-logined,
  .user .no-login {
    padding: 34px 16px 28px;
    line-height: 54px;
    color: rgba(255, 255, 255, 0.8);
}
.user-logined .left,
    .user .no-login .left {
      float: left;
      margin-right: 16px;
}
.user-logined .right i,
    .user .no-login .right i {
      font-size: 0.8125rem;
      color: rgba(255, 255, 255, 0.65);
}
.user-logined {
    position: relative;
}
.user-logined .middle {
      position: relative;
      padding: 0 70px 0 15px;
      color: rgba(255, 255, 255, 0.65);
}
.user-logined .middle .title {
        height: 1.125rem;
        margin: 6px 0 9px;
        font-size: 1.125rem;
        line-height: 1;
        color: #fff;
        word-wrap: break-word;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;
}
.user-logined .middle .intro {
        height: 0.875rem;
        font-size: 0.875rem;
        line-height: 1;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;
}
.user-logined .middle-right {
        position: absolute;
        right: 0;
        top: 50%;
        -webkit-transform: translateY(-50%);
                transform: translateY(-50%);
}
.user-logined .middle-right i {
          font-size: 0.8125rem;
}
.user-balance {
    color: #ffa42f;
}
.user-apply {
    margin-bottom: 10px;
}
.user-apply .icon-renzheng {
      position: relative;
      top: 1px;
}
.user-subimg p {
    margin-top: 0.3125rem;
}
.user-info {
    position: relative;
    width: 100%;
    height: 176px;
    background-image: url(../../assets/images/center-header.png);
    background-size: 100% 100%;
}
.user-info-wallet {
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 56px;
      padding: 0 16px;
      line-height: 56px;
      font-size: 0.875rem;
      color: rgba(255, 255, 255, 0.9);
      background-color: rgba(0, 0, 0, 0.16);
}
.user-info-wallet .icon-dianjijinru {
        margin-left: 10px;
        font-size: 0.8125rem;
        color: rgba(255, 255, 255, 0.65);
}
.user-info .icon-qianbao1 {
      margin-right: 14px;
}
.user-box {
    position: relative;
    padding: 15px 16px;
    margin-bottom: 10px;
    background: #fff;
}
.user-box-left {
      float: left;
      margin-right: 10px;
}
.user-box-right {
      position: relative;
}
.user-box-right i {
        position: absolute;
        top: 50%;
        right: 0px;
        margin-top: -6px;
        color: rgba(0, 0, 0, 0.38);
        font-size: 0.75rem;
}
.user-name {
    margin-top: 9px;
    margin-right: 16px;
    height: 15px;
    line-height: 15px;
    font-size: 0.9375rem;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.user-desciption {
    margin-top: 5px;
    font-size: 0.75rem;
    margin-right: 16px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.user-question {
    background-image: url(../../assets/icon/icon-question.png);
    background-size: contain;
}
.user-reward {
    background: url(../../assets/icon/icon-reward.png);
    background-size: contain;
}
.user-get {
    background: url(../../assets/icon/icon-get.png);
    background-size: contain;
}
.user-live {
    background: url(../../assets/icon/icon-live.png);
    background-size: contain;
}
.user-activity {
    background-image: url(../../assets/icon/icon-activity.png);
    background-size: contain;
}
.user-article {
    background: url(../../assets/icon/icon-article.png);
    background-size: contain;
}
.user-collect {
    background: url(../../assets/icon/icon-collect.png);
    background-size: contain;
}
.user-draft {
    background: url(../../assets/icon/icon-draft.png);
    background-size: contain;
}
.user-identify {
    width: 59px;
    height: 24px;
    margin-top: 15px;
    line-height: 24px;
    font-size: 0.75rem;
    text-align: center;
    border-radius: 2px;
    cursor: pointer;
    color: #fff;
    background-color: #ffa42f;
}
.user-identify.disabled {
      background-color: rgba(0, 0, 0, 0.05);
      color: rgba(0, 0, 0, 0.38);
}
.user .mb23 {
    margin-bottom: 23px;
}



