
.search {
  background-color: #f2f2f2;
}
.search-relative-item {
    height: 52px;
    padding: 0 16px;
    color: #9e9e9e;
    line-height: 52px;
    border-bottom: 1px solid #f2f2f2;
    background: #fff;
    cursor: pointer;
}
.search-relative-item i {
      font-size: 15px;
      margin-right: 3px;
}
.search-result-user {
    padding: 16px;
    border-top: 1px solid #f2f2f2;
}
.search-result em {
    font-style: normal;
    color: #ffa42f;
}
.search-result-question {
    padding: 16px;
    background: #fff;
    border-top: 1px solid #f2f2f2;
}
.search-result-question p {
      font-size: 1rem;
      color: #212121;
      line-height: 1.3125rem;
      margin-bottom: 10px;
}



