
.mgs-clomun {
  padding: 0 16px;
  background-color: #fff;
}
.mgs-clomun .avatar {
    margin-top: 16px;
    float: left;
}
.mgs-clomun-info {
    min-height: 40px;
    padding: 16px 0;
    margin-left: 52px;
    line-height: 24px;
    border-bottom: 1px solid #f2f2f2;
}
.mgs-clomun-info span {
      margin: 0 7px;
      color: rgba(0, 0, 0, 0.38);
}
.mgs-clomun-info-time {
      height: 14px;
      line-height: 14px;
      font-size: 0.875rem;
      color: rgba(0, 0, 0, 0.38);
}



