
.module-header {
  position: relative;
  width: 100%;
  height: 20px;
  font-size: 0.8125rem;
  line-height: 20px;
  color: rgba(0, 0, 0, 0.38);
}
.module-header-avatar {
    font-size: 0;
    position: absolute !important;
    top: 50%;
    -webkit-transform: translate(0, -50%);
            transform: translate(0, -50%);
}
.module-header-title {
    margin: 0 78px 0 25px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.module-header-title a {
      color: rgba(0, 0, 0, 0.38);
}
.module-header-time {
    position: absolute;
    right: 0;
    top: 0;
}



