
.article-module {
  padding: 14px 16px;
  margin-bottom: 10px;
  background: #fff;
}
.article-module:last-child {
    margin-bottom: 0;
}
.article-module-header {
    margin-bottom: 9px;
}
.article-module-content {
    font-size: 0.875rem;
    line-height: 21px;
    color: rgba(0, 0, 0, 0.54);
}
.article-module-img {
    width: 100%;
    height: 150px !important;
    margin-bottom: 10px;
}
.article-module-avatar {
    float: left;
}
.article-module-author {
    margin-left: 3px;
}
.article-module-title {
    margin: 6px 0 8px;
    font-size: 1.125rem;
    line-height: 23px;
    font-weight: 500;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
}
.article-module-content {
    margin-bottom: 14px;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
}



