
.user-role-title {
  padding: 18px 16px 9px;
  font-size: 0.8125rem;
  line-height: 0.8125rem;
  color: rgba(0, 0, 0, 0.25);
  background: #f2f2f2;
}
.user-role-item {
  position: relative;
  width: 100%;
  height: 44px;
  padding: 0 16px;
  line-height: 44px;
  font-size: 0.875rem;
  color: rgba(0, 0, 0, 0.87);
  border-bottom: 1px solid #f2f2f2;
}
.user-role-btn {
  position: absolute !important;
  top: 10px;
  right: 16px;
}



