
.find {
  background: #f2f2f2;
}
.find-search {
    position: absolute;
    top: 9px;
    width: 100%;
    background: transparent;
    z-index: 2;
}
.find-search a {
      color: #aaaaaa;
}
.find-nav {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    just-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    margin-bottom: 10px;
    background: #fff;
}
.find-nav-item {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      padding: 16px 0 13px;
      text-align: center;
      font-size: 0.875rem;
}
.find-column {
    margin-top: 10px;
}
.find-topline-icon {
    margin: 0 auto 12px;
    width: 46px;
    height: 46px;
    background: url(../../assets/icon/icon-topline.png) no-repeat;
    background-size: 100%;
}
.find-dynamic-icon {
    margin: 0 auto 12px;
    width: 46px;
    height: 46px;
    background: url(../../assets/icon/icon-dynamic.png) no-repeat;
    background-size: 100%;
}
.find-knowledge-icon {
    margin: 0 auto 12px;
    width: 46px;
    height: 46px;
    background: url(../../assets/icon/icon-dynamic.png) no-repeat;
    background-size: 100%;
}
.find-classifies {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    background: #fff;
}
.find-classifies li {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      height: 50px;
      padding: 0 16px;
      line-height: 50px;
      white-space: nowrap;
      text-align: center;
      border: 0.5px solid #f2f2f2;
}
.find-question, .find-article {
    margin-top: 1px;
}



