
.live-item {
  background: #fff;
  padding: 15px 16px 17px;
}
.live-item .left {
    float: left;
}
.live-item .right {
    margin-left: 64px;
}
.live-item .right .title {
      font-size: 1rem;
      font-weight: 500;
      color: #444;
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      overflow: hidden;
      text-overflow: ellipsis;
}
.live-item .right .author {
      height: 20px;
      margin-top: 5px;
      font-size: 0.875rem;
      color: #999;
      line-height: 20px;
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      overflow: hidden;
      text-overflow: ellipsis;
}
.live-item .right .effect {
      height: 14px;
      margin: 8px 0 15px;
      font-size: 0.8125rem;
      line-height: 14px;
      color: #999;
}
.live-item .right .effect-num {
        display: inline-block;
}



