
@charset "UTF-8";
@import url("https://at.alicdn.com/t/font_0brwrq0xn56d2t9.css");
@import url(./assets/css/reset.css);
body, html {
  min-width: 320px;
  min-height: 100%;
  height: 100%;
  font-family: -apple-system,SF UI Text,Arial,PingFang SC,Hiragino Sans GB,Microsoft YaHei,WenQuanYi Micro Hei,sans-serif;
  font-size: 16px;
}
* {
  outline: 0;
  margin: 0;
  padding: 0;
  -webkit-tap-highlight-color: transparent;
  box-sizing: border-box;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.clearfix:before {
  content: '';
  display: block;
}
.clearfix:after {
  content: '';
  display: block;
  clear: both;
  zoom: 1;
  overflow: hidden;
}
a {
  text-decoration: none;
  color: rgba(0, 0, 0, 0.87);
}
li {
  list-style: none;
}
input::-webkit-input-placeholder {
  color: rgba(0, 0, 0, 0.3);
}
input:-ms-input-placeholder {
  color: rgba(0, 0, 0, 0.3);
}
input::placeholder {
  color: rgba(0, 0, 0, 0.3);
}
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: rgba(0, 0, 0, 0.87);
  height: 100%;
}
.speakContentBox {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  padding: 0 0 0;
  overflow: auto;
  overflow-x: hidden;
  background: #f2f2f2;
  -webkit-overflow-scrolling: touch;
}
.scrollContentBox {
  position: relative;
  min-height: 105%;
  z-index: 2;
}

/*flex 布局 解决移动端输入框弹出问题*/
.flex-body {
  position: absolute;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: vertical;
  -ms-flex-direction: column;
  flex-direction: column;
  height: 100%;
  width: 100%;
  overflow: hidden;
}
.flex-main {
  position: relative;
  height: 100%;
  width: 100%;
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  overflow: hidden;
}
.flex-other {
  -webkit-flex-shrink: 0;
  -ms-flex: 0 0 auto;
  -ms-flex-negative: 0;
  flex-shrink: 0;
}
.speakBox {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.mb10 {
  margin-bottom: 10px;
}



