
.quiz-ask-content {
  padding: 0 16px;
}
.quiz-ask-content .textarea-box {
    position: relative;
}
.quiz-ask-content .textarea-box .word-num {
      position: absolute;
      right: 0;
      bottom: 12px;
      color: rgba(0, 0, 0, 0.38);
}
.quiz-ask-content textarea {
    width: 100%;
    margin: 16px 0 44px;
    height: 120px;
    line-height: 24px;
    font-size: 1rem;
    border: none;
    outline: none;
    resize: none;
}
.quiz-ask .anonymous {
  float: left;
  margin-right: 5px;
}
.quiz-ask-status {
  color: rgba(0, 0, 0, 0.38);
  font-size: 0.875rem;
  line-height: 24px;
}



