
.qa {
  box-sizing: border-box;
  width: 240px;
  height: 44px;
  padding: 0 12px;
  line-height: 44px;
  border-radius: 100px;
  background-color: #0af;
  color: #fff;
  cursor: pointer;
}
.qa-left, .qa-right {
    height: 100%;
}
.qa i {
    font-size: 1.125rem;
    margin-right: 3px;
}



