
.wellChosen-module {
  padding: 16px 16px;
  background: #fff;
}
.wellChosen-module .left {
    float: left;
}
.wellChosen-module .right {
    min-height: 54px;
    margin-left: 64px;
}
.wellChosen-module-title {
    padding-top: 6px;
    font-size: 1rem;
    font-weight: 500;
    color: rgba(0, 0, 0, 0.87);
    line-height: 16px;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
}
.wellChosen-module-content {
    margin-top: 11px;
    font-size: 0.875rem;
    color: rgba(0, 0, 0, 0.38);
    line-height: 14px;
}



