
.live-detail {
  padding-bottom: 44px;
  background: #f2f2f2;
}
.live-detail-header {
    padding: 16px 16px 20px;
    background: #fff;
}
.live-detail-header h2 {
      line-height: 24px;
      font-size: 1.5rem;
      font-weight: 500;
      color: #212121;
}
.live-detail-content {
    padding: 0 16px;
    background: #fff;
}
.live-detail-footer {
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 44px;
    background: #fff;
    box-shadow: 0 -2px 2px 0 rgba(0, 0, 0, 0.05);
}
.live-detail-footer-left {
      position: relative;
      float: left;
      box-sizing: border-box;
      width: 50%;
      padding-left: 42px;
}
.live-detail-footer-left .icon-fanhui {
        position: absolute;
        left: 13px;
        color: #1e1e1e;
        line-height: 44px;
}
.live-detail-footer-right {
      float: left;
      width: 50%;
      line-height: 44px;
      color: #fff;
      text-align: center;
      background: #0af;
}
.live-detail-footer-btn {
      width: 50%;
      height: 100%;
      float: left;
      color: rgba(0, 0, 0, 0.7);
      font-size: 0.8125rem;
      text-align: center;
}
.live-detail-footer-btn i {
        display: inline-block;
        margin-top: 9px;
        font-size: 0.8125rem;
}
.live-detail-footer-btn > p {
        font-size: 0.6875rem;
        line-height: 11px;
        margin-top: 5px;
}
.live-detail-time {
    margin-top: 13px;
    font-size: 0.875rem;
    line-height: 14px;
    color: #9e9e9e;
}
.live-detail-tags {
    margin-top: 14px;
    color: #0af;
    font-size: 0.875rem;
    line-height: 14px;
}
.live-detail-tags span {
      margin-right: 14px;
}
.live-detail-mark {
    display: inline-block;
    padding: 1px 7px;
    margin-top: -3px;
    margin-left: 4px;
    font-size: 0.6875rem;
    color: #bdbdbd;
    border: 1px solid #bdbdbd;
    border-radius: 100px;
    vertical-align: top;
}
.live-detail-user {
    padding: 22px 0px 18px;
}
.live-detail-user .default-user-name {
      margin-right: 0;
}
.live-detail-user .default-user-desciption {
      margin-right: 0;
}
.live-detail-description {
    font-size: 0.875rem;
    line-height: 23px;
    color: #616161;
    padding-bottom: 16px;
}
.live-detail-outline {
    padding: 22px 0 18px;
    font-size: 1rem;
    line-height: 16px;
    border-top: 1px solid #f2f2f2;
}
.live-detail-note {
    padding: 10px 16px 22px;
    font-size: 0.8125rem;
    color: rgba(0, 0, 0, 0.38);
}
.live-detail-note a {
      color: #0af;
      text-decoration: underline;
}



