
.module-type {
  display: block;
  box-sizing: border-box;
  position: relative;
  height: 60px;
  padding: 10px;
  background: #fdfdfd;
  border: 1px solid #f2f2f2;
  border-radius: 3px;
}
.module-type .left {
    float: left;
    height: 100%;
    margin-right: 12px;
}
.module-type .left i {
      position: relative;
      top: 9px;
      color: #0af;
      font-size: 1.25rem;
}
.module-type .left i.active {
        color: #f7766a;
}
.module-type .middle {
    margin-right: 30px;
}
.module-type .middle .title {
      height: 20px;
      font-size: 0.875rem;
      line-height: 20px;
      color: #000;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
}
.module-type .middle .info {
      height: 12px;
      margin-top: 4px;
      line-height: 12px;
      font-size: 0.75rem;
      color: rgba(0, 0, 0, 0.38);
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
}
.module-type .right {
    position: absolute;
    top: 16px;
    right: 10px;
}
.module-type .right i {
      font-size: 0.75rem;
      color: rgba(0, 0, 0, 0.87);
}



