
.quiz-header {
  position: relative;
  height: 51px;
  line-height: 51px;
  border-bottom: 1px solid #f2f2f2;
  background-color: #fff;
  text-align: center;
}
.quiz-header .left {
    position: absolute;
    left: 16px;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}
.quiz-header .left i {
      position: relative;
      top: -1px;
      margin-right: 9px;
      font-size: 0.75rem;
}
.quiz-header .right {
    position: absolute;
    right: 16px;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    color: rgba(0, 0, 0, 0.25);
}
.quiz-header .right.active {
      color: rgba(0, 0, 0, 0.87);
}



