
.img-autofit {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.img-autofit img {
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    height: 100%;
    min-height: 100%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}



