
.user-wallet {
  background: #f2f2f2;
}
.user-wallet .mb10 {
    margin-bottom: 10px;
}
.user-wallet-header {
    margin-bottom: 10px;
    text-align: center;
    background: #fff;
}
.user-wallet-balance {
    padding: 60px 0 39px;
    color: #fff;
    background-image: -webkit-linear-gradient(top, #ff6842, #ff532a);
    background-image: linear-gradient(to bottom, #ff6842, #ff532a);
}
.user-wallet-balance .price {
      font-size: 2.875rem;
      line-height: 2.125rem;
      margin-bottom: 16px;
}
.user-wallet-balance .instr {
      font-size: 0.875rem;
      line-height: 1;
      color: rgba(255, 255, 255, 0.8);
}
.user-wallet-total-left {
    float: left;
    width: 50%;
    padding: 14px 0;
}
.user-wallet-total-left .money {
      color: #ff5d35;
      font-size: 1.125rem;
      line-height: 0.875rem;
}
.user-wallet-total-left .instr {
      margin-top: 5px;
      font-size: 0.875rem;
      line-height: 0.875rem;
      color: #9e9e9e;
}
.user-wallet-total-right {
    float: right;
    width: 50%;
    padding: 14px 0;
}
.user-wallet-total-right .money {
      color: #ff5d35;
      font-size: 1.125rem;
      line-height: 0.875rem;
}
.user-wallet-total-right .instr {
      margin-top: 5px;
      font-size: 0.875rem;
      line-height: 0.875rem;
      color: #9e9e9e;
}
.user-wallet-operate .item {
    display: block;
    height: 52px;
    padding: 0 16px;
    line-height: 52px;
    background: #fff;
    border: 1px solid #f2f2f2;
}
.user-wallet-operate .item span {
      font-size: 0.875rem;
      color: rgba(0, 0, 0, 0.38);
}
.user-wallet-operate .item .icon-tixian,
    .user-wallet-operate .item .icon-youhuiquan1,
    .user-wallet-operate .item .icon-chongzhi {
      margin: 0 14px 0 4px;
}
.user-wallet-operate .item .icon-chongzhi {
      color: #0af;
}
.user-wallet-operate .item .icon-tixian {
      color: #ffa42f;
      margin: 0 14px 0 4px;
}
.user-wallet-operate .item .icon-youhuiquan1 {
      color: #f96268;
      margin: 0 14px 0 4px;
}
.user-wallet-operate .item .icon-dianjijinru {
      float: right;
      font-size: 0.8125rem;
      color: rgba(0, 0, 0, 0.38);
}
.user-wallet-detail .title {
    padding: 0 16px;
    margin: 18px 0 10px;
    font-size: 0.875rem;
    line-height: 0.875rem;
    color: rgba(0, 0, 0, 0.38);
}
.user-wallet-record li {
    padding: 17px 16px;
    background: #fff;
    border-bottom: 1px solid #f2f2f2;
}
.user-wallet-record li .instr {
      margin-top: 7px;
      font-size: 0.75rem;
      line-height: 0.8125rem;
      color: rgba(0, 0, 0, 0.38);
}



