
.search-record {
  background: #fff;
}
.search-history, .search-hot {
  padding: 16px 16px 0;
}
.search-history-title, .search-hot-title {
    margin-bottom: 14px;
    font-size: 0.875rem;
    font-weight: 500;
    color: rgba(0, 0, 0, 0.54);
}
.search-history-title i, .search-hot-title i {
      position: relative;
      top: 2px;
      font-size: 0.875rem;
      color: rgba(0, 0, 0, 0.54);
}
.search-history-box, .search-hot-box {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: flex-start;
}
.search-history-item, .search-hot-item {
    box-sizing: border-box;
    margin: 0 10px 10px 0;
    height: 31px;
    padding: 9px 10px;
    font-size: 0.8125rem;
    line-height: 13px;
    border-radius: 100px;
    background: #f2f2f2;
    cursor: pointer;
}



