
.article-item {
  padding: 14px 16px;
  margin-top: 10px;
  background: #fff;
}
.article-item:last-child {
    margin-bottom: 0;
}
.article-item-header {
    min-height: 40px;
    margin-bottom: 12px;
}
.article-item-time {
    font-size: 0.875rem;
    color: #9e9e9e;
}



