
.default-domain {
  background: #f2f2f2;
}
.default-domain-box {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: rows;
            flex-direction: rows;
    background: #fff;
}
.default-domain-selected {
    padding: 0 16px;
    margin-bottom: 10px;
    height: 44px;
    line-height: 44px;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    background: #fff;
}
.default-domain-selected .placeholder {
      text-align: center;
      color: rgba(0, 0, 0, 0.38);
}
.default-domain-selected span {
      padding: 3px 6px;
      margin-right: 3px;
      font-size: 0.75rem;
      color: #0af;
      border-radius: 100px;
      opacity: .6;
      background: #e6f7ff;
      cursor: pointer;
}



