
.default-domain {
  background: #f2f2f2;
}
.default-domain-box {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: rows;
            flex-direction: rows;
    background: #fff;
}
.default-domain-selected {
    padding: 0 16px;
    margin-bottom: 10px;
    height: 44px;
    line-height: 44px;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    background: #fff;
}
.default-domain-selected .placeholder {
      text-align: center;
      color: rgba(0, 0, 0, 0.38);
}
.default-domain-selected span {
      padding: 3px 6px;
      margin-right: 3px;
      font-size: 0.75rem;
      color: #0af;
      border-radius: 100px;
      opacity: .6;
      background: #e6f7ff;
      cursor: pointer;
}
.default-domain-item {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
}
.default-domain-item li {
      height: 54px;
      line-height: 54px;
      text-align: center;
      cursor: pointer;
}
.default-domain-item li.active {
        color: #0af;
        background: #e6f7ff;
        position: relative;
}
.default-domain-item li.active i {
          position: absolute;
          top: 0;
          right: 0;
          width: 26px;
          height: 26px;
          background: url(../../assets/icon/icon-selected.png) no-repeat;
          background-size: contain;
}
.default-domain-item.level-one {
      color: rgba(0, 0, 0, 0.87);
      font-weight: 600;
      border-right: 1px solid #f2f2f2;
}
.default-domain-item.level-two {
      color: rgba(0, 0, 0, 0.87);
      font-size: 0.875rem;
      border-right: 1px solid #f2f2f2;
}
.default-domain-item.level-third {
      color: rgba(0, 0, 0, 0.87);
      font-size: 0.875rem;
}



