
.my-question {
  background: #f2f2f2;
}
.my-question-item {
    padding: 13px 16px 0;
    margin-top: 10px;
    background: #fff;
    color: rgba(0, 0, 0, 0.87);
}
.my-question-title {
    margin: 6px 0;
    line-height: 21px;
    word-wrap: break-word;
}
.my-question-reason {
    margin-bottom: 15px;
    font-size: 0.875rem;
    color: rgba(0, 0, 0, 0.38);
}
.my-question-btn {
    min-height: 27px;
    padding: 12px 16px;
    margin: 0 -16px;
    line-height: 27px;
    font-size: 0.875rem;
    color: rgba(0, 0, 0, 0.38);
    border-top: 1px solid #f2f2f2;
}
.my-question-btn button {
      float: right;
      height: 27px;
      padding: 0 13px;
      color: #fff;
      line-height: 27px;
      background: #ffa42f;
      border-radius: 2px;
}
.my-question-operate {
    margin: 9px 0 15px;
}
.my-question-tab {
    color: rgba(0, 0, 0, 0.87);
}



