
.article-content-header {
  height: 20px;
  line-height: 20px;
  font-size: 0.8125rem;
  margin-bottom: 9px;
}
.article-content-content {
  font-size: 0.875rem;
  line-height: 21px;
  color: rgba(0, 0, 0, 0.54);
}
.article-content-img {
  width: 100%;
  height: 150px !important;
  margin-bottom: 10px;
}
.article-content-avatar {
  float: left;
}
.article-content-author {
  margin-left: 3px;
}
.article-content-title {
  margin: 6px 0 8px;
  font-size: 1.125rem;
  line-height: 23px;
  font-weight: 500;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}
.article-content-content {
  margin-bottom: 14px;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}



