
.comment-text {
  position: relative;
}
.comment-text .left {
    height: 16px;
    margin-right: 36px;
    line-height: 16px;
    font-size: 0.9375rem;
    font-weight: 600;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.comment-text .right {
    position: absolute;
    top: 0;
    right: 0;
    height: 16px;
    line-height: 16px;
    color: rgba(0, 0, 0, 0.25);
}
.comment-text .right i {
      position: relative;
      top: 1px;
      margin-right: 3px;
      color: rgba(0, 0, 0, 0.18);
}
.comment-content {
  margin-top: 5px;
  font-size: 0.9375rem;
  line-height: 23px;
}
.comment-total {
  height: 12px;
  margin-top: 8px;
  line-height: 12px;
  color: rgba(0, 0, 0, 0.38);
  font-size: 0.75rem;
}



